.list {
  display: flex;
  overflow: auto;

  gap: 65px;
  max-width: 1180px;
  width: 100%;
}

.list::-webkit-scrollbar {
  height: 4px;
}

.list::-webkit-scrollbar-track {
  background-color: #f1f1f1;
}

.list::-webkit-scrollbar-thumb {
  background-color: #888;
  border-radius: 4px;
}
